<template>
    <div id="page">
        <video-player class="video-player vjs-custom-skin"
                      ref="videoPlayer"
                      :playsinline="true"
                      :options="playerOptions"
        ></video-player>
        <div class="progress_box">
            <div class="progress_bar" v-for="item in VideoList" :key="item.id" @click.prevent="play(item)">
                <a href="#"> {{item.title}}</a>
            </div>
        </div>
        <div id="return_box">
            <a class="iconfont icon-fanhui" @click="back()"></a>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'Page',
        data() {
            return {
                VideoList: [],
                // 视频播放
                playerOptions: {
                    playbackRates: [0.7, 1.0, 1.5, 2.0], //播放速度
                    autoplay: false, //如果true,浏览器准备好时开始回放。
                    muted: false, // 默认情况下将会消除任何音频。
                    loop: false, // 导致视频一结束就重新开始。
                    preload: 'auto', // 建议浏览器在<video>加载元素后是否应该开始下载视频数据。auto浏览器选择最佳行为,立即开始加载视频（如果浏览器支持）
                    language: 'zh-CN',
                    aspectRatio: '16:9', // 将播放器置于流畅模式，并在计算播放器的动态大小时使用该值。值应该代表一个比例 - 用冒号分隔的两个数字（例如"16:9"或"4:3"）
                    fluid: true, // 当true时，Video.js player将拥有流体大小。换句话说，它将按比例缩放以适应其容器。
                    sources: [{
                        type: 'video/mp4', // 类型,//这里的种类支持很多种：基本视频格式、直播、流媒体等，具体可以参看git网址项目
                        src: "" //url地址
                    }],
                    poster: "", //你的封面地址
                    // width: document.documentElement.clientWidth, //播放器宽度
                    notSupportedMessage: '此视频暂无法播放，请稍后再试', //允许覆盖Video.js无法播放媒体源时显示的默认信息。
                    controlBar: {
                        timeDivider: true,
                        durationDisplay: true,
                        remainingTimeDisplay: false,
                        fullscreenToggle: true  //全屏按钮
                    }
                },
            }
        },
        mounted() {
            let lessonId = this.$route.params.lessonId;
            let that = this;
            this.axios.get("http://laral.jingani.cn/api/videoList/" + lessonId).then(function (response) {
                that.VideoList = response.data.data;
                that.playerOptions.sources[0].src = that.VideoList[0].path;
            })
        },
        methods: {
            play(item) {
                this.playerOptions.sources[0].src = item.path;

            },
            back(){
                this.$router.back();
            }
        },
    }
</script>

<style scoped>

    a {
        text-decoration: none;
    }

    .video-js .vjs-icon-placeholder {
        width: 100%;
        height: 100%;
        display: block;
    }

    .vjs-custom-skin > .video-js .vjs-big-play-button {
        border-radius: 50px !important;
    }

    /*课程进度条*/
    .progress_box {
        padding: 25px;
        margin: auto;
    }

    .progress_box .progress_bar {
        width: 100%;
        height: 48px;
        line-height: 48px;
        padding-left: 20px;
        position: relative;
        border-left: 2px solid #ececec;
        box-sizing: border-box;
        z-index: 0;
    }

    .progress_box .progress_bar:before {
        position: absolute;
        content: "";
        width: 6px;
        height: 6px;
        background-color: #cccccc;
        top: 50%;
        transform: translateY(-50%);
        left: -4px;
        border-radius: 50px;
    }

    .progress_box .progress_bar:first-child:after {
        content: "";
        position: absolute;
        height: 20px;
        width: 5px;
        left: -2px;
        top: 0;
        background-color: #ffffff;
        z-index: 10;
    }

    .progress_box .progress_bar:last-child:after {
        content: "";
        position: absolute;
        height: 20px;
        width: 5px;
        left: -2px;
        bottom: 0;
        background-color: #ffffff;
        z-index: 10;
    }

    .progress_box .progress_bar a {
        color: #7a7a7a;
    }

    .progress_box .progress_active a {
        color: #040404;
    }

    /*返回*/

    #return_box {
        position: fixed;
        right: 10px;
        top: 85vh;
    }

    #return_box a {
        font-size: 28px;
        color: #689F38;
    }
</style>